:host {
    font-family: sans-serif;
    border: 2px solid var(--color-primary, black);
    margin: 2rem;
    padding: 1rem;
    display: block;
    width: 20rem;
    max-width: 100%;
}

form input {
    font: inherit;
    color: var(--color-primary, black);
    padding: 0.1rem 0.25rem;
    display: block;
    margin-bottom: 0.5rem;
}

form input:focus,
form button:focus {
    outline: none;
}

form button {
    font: inherit;
    padding: 0.25rem 0.5rem;
    border: 1px solid var(--color-primary, black);
    background-color: var(--color-primary, black);
    color:var(--color-primary-inverse,white);
    cursor: pointer;
}

form button:hover,
form button:active {
    background-color: var(--color-primary-highlight, grey);
    border-color:  var(--color-primary-highlight, grey);
}

form button[disabled] {
    background-color: lightgray;
    border-color: lightgray;
    color: white;
    cursor: not-allowed;
}

ul {
    margin: 0;
    padding:0;
    list-style: none;
}

li {
    margin: 0.25rem ;
    padding: 0.5rem;
    border: 1px solid #ccc;
    cursor: pointer;

}

li:hover, 
li:active {
    background-color: var(--color-primary, black);
    color: var(--color-primary-inverse, white);
}